import React, { Component } from 'react';
import axios from 'axios';
import {url_machines} from '../../../urls'
import { withTranslation, Trans } from 'react-i18next';
import ContentWrapper from '../Layout/ContentWrapper';
import { Row, Col } from 'reactstrap';
import MapMachine from '../Maps/MapMachine';
import ApercuMachine from '../Dashboard/ApercuMachine';
import HeadingMenu from '../HeadingMenu';
import _ from 'lodash';

const containerStyle = {
    width: '100%',
    height: '600px'
}

const path = [
    { 
        label: 'Home',
        value: '/dashboard_machine',
    },{ 
        label: 'Carte',
        value: '/carte_machine',
    },
]

const defaultZoom = 12;

class Carte extends Component {
    constructor(props, context) {
        super(props, context);
        //this.ref = firebase.firestore().collection('machines');
        //this.unsubscribe = null;
        this.state = { 
            machines: [],
            selectedMachine: {}, //id de la machine courante
            isLoading: true,
            isSelectMachine: false,
            zoom: defaultZoom,
        }; 
    }
    
    componentDidMount() {
        // liste des machines
        axios.get(url_machines)
        .then(res => {
            const machines = res.data.data.machines
            console.log(machines);
            this.setState({
                machines: machines,
                isLoading: false,
                zoom: 12
             });
        })
        .catch((error) => {
            console.log("get machine failed!")
            console.log(error)
        })
    }

    selectMachine = e => {
        const {lat, lng} = e.latLng.toJSON()
        const position = {
            latitude: lat.toString(),
            longitude: lng.toString(),
        }
         
        const machine = this.state.machines.find(machine => _.isEqual(machine.coordonnees, position))
        console.log(machine)
        this.setState({
            selectedMachine: machine,
            isSelectMachine: true,
            zoom: 18,
        })
    }

    resizeMap = () => {
        this.setState({
            zoom: defaultZoom,
        })
    }

    render() {
        const machines = this.state.machines
        const selectedMachine = this.state.selectedMachine
        const zoom = this.state.zoom

        console.log(machines)
        const isLoading = !this.state.isLoading
        const isSelectMachine = this.state.isSelectMachine

        return (
          isLoading? (
            <ContentWrapper>
                <HeadingMenu path={path}/>
                <Row>
                    <Col md={8}>
                        <MapMachine 
                         listeMachine={machines}
                         currentMachine={selectedMachine}
                         isSelect={isSelectMachine}
                         zoom={zoom}
                         containerStyle={containerStyle}
                         onClick={this.selectMachine}
                         onResize={this.resizeMap}
                        />
                    </Col>
                    {isSelectMachine? (
                    <Col md={4}>
                        <ApercuMachine 
                         currentMachine={selectedMachine}
                         isSelect={isSelectMachine}
                         />
                    </Col>
                    ):(
                        <div className="d-flex">
                            <span className="align-self-center align-items-center"> Selectionner une machine et retrouvez ses informations </span>  
                        </div>
                    )}    
                </Row>
            </ContentWrapper>
          ):(
            <span> En cours de chargement...</span>
          )
    );

    }

}

export default withTranslation('translations')(Carte);
