import React, { Component } from 'react';
import { Progress, Row, Col, Card, CardHeader, CardBody, CardFooter, Input } from 'reactstrap';

const etatFalse = {
    label: 'déconnectée',
    color: 'circle circle-lg bg-danger',
}

const etatTrue = {
    label: 'connectée',
    color: 'circle circle-lg bg-success',
}

class ApercuMachine extends Component {
    constructor(props) {
        super(props);
        
    }

    render() {
        console.log(this.props.currentMachine)
        const { nom, localite, level, status, qte_bte, modele } = this.props.currentMachine
        const {libelle, description} = new Object(modele)
        const {pays, ville, quartier, lieu} = new Object(localite);
        //const libelle =  'A-18'
        //const description = 'Machine de rue'
        const nb_bte = qte_bte
        const nb_tickect = 18

        //Status formatter
        let etat = {}
        status? (
            etat = etatTrue
        ):(
            etat = etatFalse
        )
        //end status formatter
        // level formatter
        let levelFormatter = { levelColor: 'danger', value: level }
        level == 0 ? (
            levelFormatter = { levelColor: 'gray-light', value: level }
        ): level <= 70 ? (
            levelFormatter = { levelColor: 'success', value: level } 
        ): level <=85 ? (
            levelFormatter = { levelColor: 'success', value: level }
        ):(
            console.log(levelFormatter)
        )
        // end level formatter
    
        const {label, color} = etat
        const {value, levelColor} = levelFormatter


        // 
        return (
            
            <div className="row">
                    {/*Info Machine */}
                    <Card outline color="info" >
                            <CardHeader className="mr-3 mt-3 bg-gray-light">
                                <div className="float-right row">
                                    {/*<span className="align-self-center"> {label} </span>*/}
                                    <div className={color}/>
                                </div>
                                <h4 className="m-0">{nom}</h4>
                                <small className="text-muted"></small>
                            </CardHeader>
                            <CardBody>
                                <div className="d-flex align-items-center mb-4 ">
                                    <div className="w-100" data-title="Health">
                                        { level==0? (
                                            <Progress className="progress-xs m-0" value={100} color={levelColor}> </Progress>
                                        ):(
                                            <Progress className="progress-xs m-0" value={level} color={levelColor}> </Progress>

                                        )}
                                    </div>
                                    <div className="wd-xxs text-right">
                                        <div className="text-bold text-muted">{level}%</div>
                                    </div>
                                </div>
                            <div className="mb-3 mt-3">                            
                                {/*Adresse */}
                                <div className="media mb-3 pl-2">
                                    <div className="align-self-start mr-2">
                                        <em className="fas fas fa-map-marker-alt fa-2x"></em>
                                    </div>
                                    <div className="media-body text-center">
                                        <address>
                                            <strong> {lieu} </strong>
                                            <br/> {quartier}
                                            <br/> 
                                            <span> {ville} - {pays} </span>
                                        </address>
                                    </div>
                                </div>                            
                                {/*Modele */}
                                <div className="media mb-4 pl-2">
                                    <div className="align-self-start mr-2">
                                        <em className="fas fa-box fa-2x"></em>
                                    </div>
                                    <div className="media-body text-center">
                                        <span> {libelle} </span>
                                        <br/>
                                        <span className="pl-3"> {description} </span>
                                    </div>
                                </div>                            
                                {/*Chiffres */}
                                <label className="mr-3 pl-1 mb-3">Ajourdh'ui...</label>
                                {/*bte collectees */}
                                <div className="media mb-4">
                                    <div className="align-self-center mr-2">
                                        <em className="fas fas fa-check fa-2x text-success"></em>
                                    </div>
                                    <div className="media-body text-center">
                                        <h4 className="mt-0 text-bold">{nb_bte}</h4>
                                        {nb_bte <= 1? (
                                            <p className="mb-0 text-muted">BOUTEILLE COLLECTEE </p>
                                        ):(
                                            <p className="mb-0 text-muted">BOUTEILLES COLLECTEES</p>
                                        )}
                                    </div>
                                </div>
                                {/*tickets imprimés */}
                                <div hidden className="media mt-4 mb-3">
                                    <div className="align-self-center mr-2">
                                        <em className="fas fas fa-check fa-2x text-success"></em>
                                    </div>
                                    <div className="media-body text-center">
                                        <h4 className="mt-0 text-bold">{nb_tickect}</h4>
                                        <p className="mb-0 text-muted text-bold">TICKETS IMPRIMES</p>
                                    </div>
                                </div>
                            </div>
                            </CardBody>
                            {/*<CardFooter className="text-center mb-auto mt-2"color="bg-gray">
                            </CardFooter>*/}
                    </Card>
                </div>
        ); 
    }
}

export default ApercuMachine;


