import React, { Component } from 'react';
import ContentWrapper from '../Layout/ContentWrapper';
import { Card, CardHeader, CardBody, CardFooter } from 'reactstrap';
import { GoogleMap, LoadScript, Marker } from '@react-google-maps/api';
import { googleMapsApiKey} from '../../../allconfig';

const svgMarker = {
    path: "M10.453 14.016l6.563-6.609-1.406-1.406-5.156 5.203-2.063-2.109-1.406 1.406zM12 2.016q2.906 0 4.945 2.039t2.039 4.945q0 1.453-0.727 3.328t-1.758 3.516-2.039 3.070-1.711 2.273l-0.75 0.797q-0.281-0.328-0.75-0.867t-1.688-2.156-2.133-3.141-1.664-3.445-0.75-3.375q0-2.906 2.039-4.945t4.945-2.039z",
    fillColor: "green",
    fillOpacity: 1,
    strokeWeight: 0,
    rotation: 0,
    scale: 2,
    //anchor: new google.maps.Point(15, 30),
};

const FullMarker = props => (
    <Marker 
    key={props.key} 
    position={props.position}
    onClick={props.onClick}
    />
)

const LowMarker = props => (
    <Marker 
    key={props.key} 
    position={props.position}
    onClick={props.onClick}
    icon={svgMarker}
    title={props.title}
    />
)

// Demo with multiple markers
const DemoMapCustomMarkers = props => {
    console.log(props)
    const test = true
    return (
        props.customMarkers.length ? (
            <GoogleMap 
                mapContainerStyle={props.containerStyle} 
                zoom={props.zoom} 
                center={props.center}
            >
                {props.customMarkers.map( (machine) =>
                    //test? (
                        <FullMarker 
                        key={machine.key} 
                        position={machine.value}
                        onClick={props.onClick}
                        //title={machine.nom}
                        />
                   /* ):(
                        <LowMarker 
                        key={i} 
                        position={loc}
                        onClick={props.onClick}
                        />  
                    )*/
                )}

            </GoogleMap>
        ):(
            <GoogleMap mapContainerStyle={props.containerStyle} zoom={props.zoom} center={props.center}/>
        )
    )
}
 

//default value for lat, long
const defaultPosition = {
    lat: 4.0004737, 
    lng: 9.8057055
}

class MapMachine extends Component {
    constructor(props) {
        super(props);
        this.state = {
            center: {},
            customStyle: [
                {featureType:'water',stylers:[{visibility:'on'},{color:'#bdd1f9'}]},
                {featureType:'all',elementType:'labels.text.fill',stylers:[{color:'#334165'}]},
                {featureType:'landscape',stylers:[{color:'#e9ebf1'}]},
                {featureType:'road.highway',elementType:'geometry',stylers:[{color:'#c5c6c6'}]},
                {featureType:'road.arterial',elementType:'geometry',stylers:[{color:'#fff'}]},
                {featureType:'road.local',elementType:'geometry',stylers:[{color:'#fff'}]},
                {featureType:'transit',elementType:'geometry',stylers:[{color:'#d8dbe0'}]},
                {featureType:'poi',elementType:'geometry',stylers:[{color:'#cfd5e0'}]},
                {featureType:'administrative',stylers:[{visibility:'on'},{lightness:33}]},
                {featureType:'poi.park',elementType:'labels',stylers:[{visibility:'on'},
                {lightness:20}]},{featureType:'road',stylers:[{color:'#d8dbe0',lightness:20}]}
            ]
        }
    }

    onResize = this.props.onResize;

    render() {
        // get all position
        const machines = this.props.listeMachine
        //console.log(machines)
        const positions = []
        machines.map((machine) => {
            positions.push({
                key: machine.key,
                value :{
                    lat: parseFloat(machine.coordonnees.latitude),
                    lng: parseFloat(machine.coordonnees.longitude)
                },
                nom: machine.nom,
                //level: machine.level,
            })
        })
        const isSelect = this.props.isSelect
        let zoom = this.props.zoom
        console.log(this.state.center)
        
        let center = positions[0].value
        if (isSelect) {
            center = this.props.currentMachine.coordonnees
        }
        console.log(center)
        const containerStyle = this.props.containerStyle

        return (
            <LoadScript
                googleMapsApiKey = {googleMapsApiKey}
            >
                <ContentWrapper>
                    <Card className="card-default">
                        <CardBody>
                            <DemoMapCustomMarkers 
                             customMarkers={positions}
                             center={center}
                             zoom = {zoom} 
                             containerStyle={containerStyle}
                             onClick={this.props.onClick}
                            />
                        </CardBody>
                        <CardFooter className="ml-auto">
                            <span className="fas fa-compress pl-auto text-dark fa-2x" onClick={this.onResize}> </span>
                        </CardFooter>
                    </Card>
             </ContentWrapper>
            </LoadScript>
        );

        //return(<div> Just for test</div>)
    }

}

export default MapMachine;

